.hamburger
    width: 36px
    height: 21px
    position: absolute
    top: 20px
    right: 30px
    z-index: 999
    display: flex
    flex-direction: column
    justify-content: space-around
    align-items: flex-end
    cursor: pointer
    &__item
        width: 30px
        height: 3px
        background-color: $second-color
        &:nth-child(2)
            width: 36px

            