.menu
    height: 100%
    width: 100%
    position: absolute
    top: 0
    opacity: 0
    visibility: hidden
    transition: 0.5s all linear
    &_active
        opacity: 1
        visibility: visible
    &__overlay
        height: 100%
        width: 100%
        position: absolute
        top: 0
        left: 0
        background-color: $second-color
        opacity: 0.75
    &__area
        height: 100%
        width: 345px
        position: relative
        left: -100%
        z-index: 10
        padding-top: 140px
        padding-left: 75px
        background-color: $main-color
        transition: 0.5s all linear
        &_active
            left: 0
    &__list
        margin: 0
        padding-inline-start: 0
        list-style-type: none
    &__item
        position: relative
        margin-bottom: 15px
        &::before
            content: ''
            position: absolute
            left: -35px
            top: 50%
            transform: translateY(-50%)
            height: 15px
            width: 15px
            border-radius: 100%
            border: 1px solid #000000
    &__link
        font-weight: 700
        font-size: 24px
        line-height: 36px
        color: $second-color
        text-decoration: none
        text-transform: uppercase
        transition: 0.5s all linear
        &:hover
            color: #000000
    &__close
        position: absolute
        top: 35px
        right: 25px
        width: 29px
        height: 29px
        cursor: pointer
    &__close-item
        display: block
        width: 40px
        height: 4px
        background-color: #000000
        &:first-child
            transform: rotate(45deg) translateY(3px)

        &:last-child
            transform: rotate(-45deg) translateY(-4px)
    &__socials
        width: 90px
        position: absolute
        left: 50%
        transform: translateX(-50%)
        bottom: 50px
        display: flex
        justify-content: space-between
    &__social-link
        width: 20px
        height: 20px
    
    
            

        
