@mixin round-before
    &::before    
        content: ''
        position: absolute
        left: 0
        top: 50%
        transform: translateY(-50%)
        z-index: -1
        width: 36px
        height: 36px
        border-radius: 100%
        background-color: $main-color

@mixin describer
    &::before
        content: ''
        position: absolute
        left: 12px
        top: -50%
        transform: translateY(50%)
        padding: 0 8px
        font-weight: 500
        font-size: 12px
        line-height: 18px
        color: #000000
    