.works
    padding-top: 120px
    &__wrapper
        margin-top: 60px
        display: grid
        grid-template: repeat(2, 280px) / repeat(4, 280px)
        grid-auto-rows: 280px
        gap: 5px
    &__link-item
        transition: all 0.4s linear
        &:hover
            transform: scale(1.05)     
    &__link-img
        width: 100%
.vertical
    grid-column: 4/5
    grid-row: 1/3
.horizontal
    grid-column: 2/4
    grid-row: 2/3

        