.experience
    padding-top: 120px
    &__wrapper
        margin: 60px 89px 0 65px
        display: grid
        grid-template-columns: repeat(2, 1fr)
        column-gap: 136px
    &__column
        padding-left: 7px
    &__column-title
        padding-left: 43px
        position: relative
        z-index: 2
        @include round-before
        &::before
            width: 15px
            height: 15px
            left: -7px
    &__item
        margin-top: 45px
        padding: 0 15px 0 43px 
        position: relative
        z-index: 2
        display: grid
        grid-template: repeat(2, minmax(36px, auto)) / 36px 1fr
        column-gap: 20px
        &::before
            content: ''
            width: 1px
            height: calc( 100% + 45px )
            position: absolute
            z-index: -1
            left: 0
            top: -53px
            background-color: #000
    &__circle
        position: relative
        z-index: 2
        padding: 15px 0 0 15px
        @include round-before
        &::before
            top: 0
            transform: translateY(0)
        &::after
           content: ''
           width: 42px
           height: 1px
           position: absolute
           left: -42px
           top: 50%
           transform: translateY(-50%)
           background-color: #000
    &__descr
        font-size: 12px
        line-height: 18px
    &__text
        grid-column: 1/3
        margin-top: 12px
        margin-bottom: 0
        font-size: 12px
        line-height: 18px