.promo
    height: 100vh
    background: url('../img/header_bg.jpg') center (center/cover) no-repeat
    position: relative
    padding-top: 200px
    &__title
        width: 450px
        margin-top: 9px
    &__subtitle
        position: relative
        z-index: 2
        padding-left: 10px
        @include round-before
    &__links
        width: 250px
        display: flex
        justify-content: space-between
        align-items: center
        margin-top: 25px
    &__link-item
        font-weight: 700
        font-size: 14px
        line-height: 21px
        color: $second-color
        text-decoration: none
        &:hover
            color: $second-color
    &__socials
        width: 340px
        position: fixed
        left: 50px
        top: 50%
        transform: translate(-50%, -50%) rotate(-90deg) 
        display: flex
        justify-content: space-around
        align-items: center
    &__social-text
        font-weight: 700
        font-size: 16px
        line-height: 24px
        color: $main-color
    &__divider
        width: 70px
        height: 2px
        background-color: $main-color
    &__social-link
        width: 20px
        height: 20px

    
    