.tools
    padding-top: 120px
    &__wrapper
        margin-top: 60px
        display: grid
        grid-template: repeat(2, minmax(270px, auto)) / repeat(4, 270px)
        grid-auto-rows: minmax(270px, auto)
        row-gap: 13px
        justify-content: space-around
    &__item 
        padding: 30px 29px 25px 30px
        border: 1px solid #000000
    &__title
        margin-top: 20px
    &__descr
        margin-top: 15px
        margin-bottom: 0
        font-size: 12px
        line-height: 18px
        color: #000000
    &__img
        height: 65px
        line-height: 65px
    &__usage
        margin-top: 120px
        display: grid
        grid-template: 70px / repeat(3, 350px)
        gap: 30px 45px
    &__usage-header
        display: flex
        justify-content: space-between
    &__usage-title
        position: relative
        z-index: 2
        padding-left: 14px
        @include round-before
    &__usage-count
        font-size: 14px
        line-height: 21px
        color: #000000
    &__usage-scale
        width: 100%
        height: 14px
        margin-top: 25px
        background-color: #E8E4DE
        border-radius: 4px
    &__usage-range
        height: 100%
        border-radius: 4px
        background-color: $main-color

