.title    
    font-weight: 700
    font-size: 48px
    line-height: 60px
    color: #000000
    margin-bottom: 0
    &_fz14
        font-size: 14px
        line-height: 21px
    &_fz16
        font-size: 16px
        line-height: 24px
    &_fz20
        font-size: 20px
        line-height: 30px  
    &_fz36
        font-size: 36px
        line-height: 60px
    &_round
        margin-top: 6px
        padding-left: 0
        text-align: center
        position: relative
        z-index: 2
        @include round-before
        &::before
            top: -6px
            left: 50%
            transform: translateX(-50%)
    &_center
        margin-top: 6px
        text-align: center
    &_white
        color: $second-color
    