.price
    padding-top: 60px
    &__wrapper
        width: 1040px
        margin: 0 auto
        display: grid
        grid-template-columns: repeat(2, 366px)
        justify-content: space-between
    &__list-column
        list-style-type: none
        padding-left: 25px
        margin-top: 60px
        margin-bottom: 0
    &__list-item
        margin-bottom: 15px
        display: grid
        grid-template: 21px 18px / repeat(2, auto)
        justify-content: space-between
        row-gap: 5px
        &:last-child
            margin-bottom: none
    &__round
        position: relative
        z-index: 2
        @include round-before
        &:before
            width: 15px
            height: 15px
            left: -25px
    &__count
        justify-self: end
        font-weight: 700
        font-size: 14px
        line-height: 21px
        color: #000000
    &__descr
        grid-column: 1/3
        font-size: 12px
        line-height: 18px
        color: #000000
