.about
    padding-top: 120px
    &__area
        display: grid
        grid-template-columns: 400px 570px
        column-gap: 70px
    &__photo
        width: 100%
    &__descr 
        padding-top: 6px
    &__title
        position: relative
        z-index: 2
        padding-left: 10px
        @include round-before
    &__divider
        margin: 4px 0 0 0
    &__text
        margin: 20px 0 0 0
        font-size: 14px
        line-height: 21px
        color: #000000
    &__ability
        margin-top: 40px
        display: grid
        grid-template: repeat(2, minmax(116px, auto)) / repeat(2, 260px)
        grid-auto-rows: minmax(116px, auto)
        gap: 20px 50px
    &__ability-item
        display: grid
        grid-template-columns: 40px 200px
        column-gap: 20px  
    &__ability-circle
        position: relative
        z-index: 2
        padding: 15px 0 0 15px
        @include round-before
        &::before
            top: 0
            transform: translateY(0)
    &__ability-text
        margin: 6px 0 0 0
        font-size: 12px
        line-height: 18px
        color: #000000
